@font-face {
    font-family: 'Roboto';
    src: url(${___CSS_LOADER_URL_REPLACEMENT_0___}) format('woff2'),
        url(${___CSS_LOADER_URL_REPLACEMENT_1___}) format('woff');
    font-weight: normal;
    font-style: normal;
    font-display: swap;
}


body {
    
    margin: 0px;
    padding: 0px;
    font-family: 'Roboto', Helvetica, Arial,  sans-serif;

    /* custom prop */
    --btn-bg-color: rgb(228, 118, 137);
    --btn-border-color: rgb(175, 102, 102);
    --btn-hover-color: rgb(211, 106, 124);
    /* layout */
    height: 100vh;
    overflow-y: hidden;
    gap: 15px;
    text-overflow: hidden;
}



#newTodoBtn {
    background-color: var(--btn-bg-color);
    font-size: min(4.5rem,70px);
    color: white;
    padding: 0px 20px 0px;
    border: 5px solid var(--btn-border-color);
    border-radius: 90px;
    position: absolute;
    box-shadow: 5px 6px 13px 3px rgba(0,0,0,0.34);
    bottom: 70px;
    right: 20px;
}

#newTodoBtn:hover {
    background-color: var(--btn-hover-color);
    /* border: 5px solid rgb(194, 137, 163); */
}


#newTodoBtn:active {
    bottom: 68px;
    right: 18px;
}

.wrapper {
    display: flex;
    
}

.wrapper > div { /*side and main divs*/
    --block-padding: 20px;
    padding: var(--block-padding);
    font-size: 1.2rem;
    display: flex;
    flex-direction: column;
}

/* sidebar styles */
#sidebar {
    height: calc(100vh - 15px - 15px - 1.5rem - 2 * var(--block-padding)); 
    width: min(30%,300px);
    background: rgb(245, 238, 238);
    overflow-y: auto;
}

#sidebarProjCon {
    display: flex;
    justify-content: space-between;
    align-items: center;

    & #createProjBtn {
        height: min-content;
        padding: 5px 10px 5px;
    }
}

#sidebar > div {
    display: flex;
    flex-direction: column;
    gap: 10px;
}

#sidebar button {
    background-color: rgb(245, 238, 238);
    border: 1px solid  rgb(245, 238, 238);
    font-size: 1.5rem;
    color: rgb(160, 94, 94);
    font-weight: bold;
    border-radius: 25px;
    padding: 5px;
    text-wrap: wrap;
    overflow-wrap: break-word;
}

#sidebar button:hover {
    transition: 0.4s;
    background-color: rgb(218, 207, 207);
}

#sidebar .selected {
    background-color: rgb(196, 180, 180);
    color: rgb(88, 58, 58);
}

#sidebar .selected:hover {
    background-color: rgb(196, 180, 180);
}

.projTabIntermediate {
    border: 3px solid var(--btn-border-color);
    border-radius: 15px;
    outline: none;
    background : #eeeeee00;
    border-radius: 25px;
    padding: 5px;
    font-weight: bold;
    font-size: 1.5rem;
    color: rgb(160, 94, 94);
}

.projDelBtn {
    background: white;
    font-size: 1.2rem;
    color: var(--btn-border-color);
    border: 2px solid var(--btn-border-color);
    border-radius: 5px;
}

.projDelBtn:hover {
    background: rgb(240, 227, 227);
}

.projDelBtn:active {
    font-size: 3rem;
}

/* main stuff */
#main {
    box-sizing: border-box;
    display: flex;
    flex-direction: column; 
    width: 100%;
    height: 100%;
    /* height: 100vh - 15px - 15px - 1.5rem; */
    gap: 15px; 
    /* padding-right: 100px; */

    & .todo {
        display: flex;
        flex-direction: column;
        border-left: 5px solid rgb(190, 156, 92);
        border-radius: 10px;
        padding: 10px;
        background-color: rgb(236, 217, 217);

        & .row1,.row2 {
            display: flex;
            gap: 10px;
            align-items: center;
            flex-wrap: wrap;
        }

        & .row1 {
            position: relative;
            justify-content: space-between;
        }

        & .row2 {
            padding-left: 24px;
            justify-content: space-between;
        }
    }

    & .todo:hover {
        background-color: rgb(226, 206, 206);
    }
}

#scrollContainer {
    width: 70%;
    height: calc(100vh - 15px - 15px - 1.5rem - 2 * var(--block-padding));
    /* padding-bottom: 0.5px; */
    overflow-y: auto;
    gap: 15px;
}

.flexCon {
    display: flex;
    flex-wrap: wrap;
    justify-content: left;
    align-items: center;
}

.todo.trivial {
    border-left: 5px solid rgb(61, 163, 61) !important;
}

.todo.important {
    border-left: 5px solid rgb(190, 92, 92) !important;
}

.todoTitle {
    padding-left: 26px;
    font-size: 1.4rem;
    margin-left: 5px;
}

.todoProj,.todoDateGrp {
    color:rgb(90, 90, 90);
}

.todoProj {
    margin-left: 5px;
}

.todoProj::before {
    content: "#";
}

.todoDateGrp {
    width: 220px;
}

/* .todoDateGrp::before {
    content: "By: "
} */

.editBtn {
    width: 25px;
    padding: 3px 0px 5px;
    border-radius: 10px;
}

.deleteBtn {
    background-color: rgba(240, 255, 255,0);
    border: 0px;
    border-radius: 10px;
    font-size: 1.2rem;
}

.deleteBtn:hover{
    background-color: rgba(255, 255, 255, 0.637);
    cursor: pointer;
}

.editBtn:active,.deleteBtn:active {
    background-color: rgb(255, 249, 249);
}

.checkCon {
    position: absolute;
    margin-right: 15px;
}

.checkCon:hover * {
    background-color: #e9e9e9;
}

input[type=checkbox] {
    position: absolute;
    opacity: 1;
    height: 50;
    width: 50;
}

.checkmark {
    position: absolute;
    top: 0;
    left: 0;
    height: 20px;
    width: 20px;
    background-color: #ffffff;
    border: 1px solid var(--btn-border-color);
    border-radius: 5px;
}

.checkCon input:checked ~ .checkmark {
    background-color: var(--btn-bg-color);
}

.checkmark:after {
  content: "";
  position: absolute;
  display: none;
}

.checkCon input:checked ~ .checkmark:after {
    display: block;
}

.checkCon .checkmark:after {
  left: 6px;
  top: 3px;
  width: 5px;
  height: 10px;
  border: solid white;
  border-width: 0 3px 3px 0;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
}

.complete {
    opacity: 0.6 !important;
}


footer {
    font-size: min(1.2rem,30px);
    line-height: 1.5rem;
    background-color: rgb(187, 98, 113);
    color: white;
    font-weight: bold;
    text-align: center;
    padding: 15px;
}



/* New todo modal */
::backdrop {
    background: rgb(206, 200, 200);
    opacity: 0.50;
}

dialog {
    padding: 0px;
    border: 3px solid grey;
    border-radius: 15px;
    font-size: 1.5rem;
}

dialog > div {
    padding: 24px;
}

dialog input {
    margin-top: 10px;
    border: 0px;
    border-bottom: 2px solid lightgrey;
    padding: 10px 20px 10px;
    font-size: 1.5rem;
}

label[for="desc"], label[for="editDesc"] {
    display: block;
}

textarea {
    display: block;
    font-family: "Roboto","Arial";
    font-size: 1.2rem;
    resize: none;
}

input:hover {
    background-color: rgb(235, 235, 235);
}

dialog input:focus {
    outline: none;
    border: 0px;
    border-bottom: 2px solid lightgrey;
}

#priorityCon {
    display: flex;
    gap: 15px;
    align-items: center;
}

#priorityCon > button {
    font-size: 1.2rem;
    border-radius: 15px;
    padding: 3px 5px 3px;
}

#priorityCon > button.selected {
    /* background-color: rgb(223, 223, 223); */
    border: 5px solid var(--btn-border-color);
    position: relative;
    top: -2px;
    left: -2px;
    box-shadow: 5px 5px 13px 3px rgba(0,0,0,0.34);
}

dialog button:active {
    position: relative;
    top: 1px;
    left: 1px;
}

#trivialBtn {
    background-color: rgb(199, 245, 199);
    border: 5px solid rgb(40, 126, 40);
}

#trivialBtn:hover {
    background-color: rgb(168, 214, 168);
}



#standardBtn {
    background-color: rgb(245, 237, 199);
    border: 5px solid rgb(126, 113, 40);
}

#standardBtn:hover {
    background-color: rgb(214, 206, 168);
}



#imptBtn {
    background-color: rgb(245, 199, 199);
    border: 5px solid rgb(126, 40, 40);
}

#imptBtn:hover {
    background-color: rgb(214, 171, 168);
}



.buttongrp {
    display: flex;
    justify-content: right;
}

button.submit,button.edit {
    padding: 7px;
    font-size: 1.2rem;
    background-color: var(--btn-bg-color);
    font-weight: bold;
    color: white;
    border-radius: 10px;
    border: 3px solid  var(--btn-border-color);
}

button.submit:hover,button.edit:hover {
    background-color: var(--btn-hover-color);
}

#Pcontainer {
    display: flex;
    gap: 20px;
    align-items: center;
}
















select, ::picker(select) {
    appearance: base-select;
}

select {
    border: 2px solid #ddd;
    border-radius: 15px;
    font-size: 1.2rem;
    padding: 5px;
    width: 7rem;
}

select:hover {
    background: rgb(216, 186, 186);

}

select::picker-icon {
    rotate: 180deg;
}

::picker(select) {
  border: none;
}

option {
    display: flex;
    justify-content: flex-start;
    gap: 20px;
    padding: 5px 3px 5px;
    border: 2px solid #ddd;
    background: #eee;
    border-radius: 10px;
}

option:hover {
    background: rgb(216, 186, 186);
}

option:focus {
    background: rgb(216, 186, 186);
}

selectedcontent .icon {
  display: none;
