1.5rem;\n    color: rgb(160, 94, 94);\n    font-weight: bold;\n    border-radius: 25px;\n    padding: 5px;\n    text-wrap: wrap;\n    overflow-wrap: break-word;\n}\n\n#sidebar button:hover {\n    transition: 0.4s;\n    background-color: rgb(218, 207, 207);\n}\n\n#sidebar .selected {\n    background-color: rgb(196, 180, 180);\n    color: rgb(88, 58, 58);\n}\n\n#sidebar .selected:hover {\n    background-color: rgb(196, 180, 180);\n}\n\n.projTabIntermediate {\n    border: 3px solid var(--btn-border-color);\n    border-radius: 15px;\n    outline: none;\n    background : #eeeeee00;\n    border-radius: 25px;\n    padding: 5px;\n    font-weight: bold;\n    font-size: 1.5rem;\n    color: rgb(160, 94, 94);\n}\n\n.projDelBtn {\n    background: white;\n    font-size: 1.2rem;\n    color: var(--btn-border-color);\n    border: 2px solid var(--btn-border-color);\n    border-radius: 5px;\n}\n\n.projDelBtn:hover {\n    background: rgb(240, 227, 227);\n}\n\n.projDelBtn:active {\n    font-size: 3rem;\n}\n\n/* main stuff */\n#main {\n    box-sizing: border-box;\n    display: flex;\n    flex-direction: column; \n    width: 100%;\n    height: 100%;\n    /* height: 100vh - 15px - 15px - 1.5rem; */\n    gap: 15px; \n    /* padding-right: 100px; */\n\n    & .todo {\n        display: flex;\n        flex-direction: column;\n        border-left: 5px solid rgb(190, 156, 92);\n        border-radius: 10px;\n        padding: 10px;\n        background-color: rgb(236, 217, 217);\n\n        & .row1,.row2 {\n            display: flex;\n            gap: 10px;\n            align-items: center;\n            flex-wrap: wrap;\n        }\n\n        & .row1 {\n            position: relative;\n            justify-content: space-between;\n        }\n\n        & .row2 {\n            padding-left: 24px;\n            justify-content: space-between;\n        }\n    }\n\n    & .todo:hover {\n        background-color: rgb(226, 206, 206);\n    }\n}\n\n#scrollContainer {\n    width: 70%;\n    height: calc(100vh - 15px - 15px - 1.5rem - 2 * var(--block-padding));\n    /* padding-bottom: 0.5px; */\n    overflow-y: auto;\n    gap: 15px;\n}\n\n.flexCon {\n    display: flex;\n    flex-wrap: wrap;\n    justify-content: left;\n    align-items: center;\n}\n\n.todo.trivial {\n    border-left: 5px solid rgb(61, 163, 61) !important;\n}\n\n.todo.important {\n    border-left: 5px solid rgb(190, 92, 92) !important;\n}\n\n.todoTitle {\n    padding-left: 26px;\n    font-size: 1.4rem;\n    margin-left: 5px;\n}\n\n.todoProj,.todoDateGrp {\n    color:rgb(90, 90, 90);\n}\n\n.todoProj {\n    margin-left: 5px;\n}\n\n.todoProj::before {\n    content: \"#\";\n}\n\n.todoDateGrp {\n    width: 220px;\n}\n\n/* .todoDateGrp::before {\n    content: \"By: \"\n} */\n\n.editBtn {\n    width: 25px;\n    padding: 3px 0px 5px;\n    border-radius: 10px;\n}\n\n.deleteBtn {\n    background-color: rgba(240, 255, 255,0);\n    border: 0px;\n    border-radius: 10px;\n    font-size: 1.2rem;\n}\n\n.deleteBtn:hover{\n    background-color: rgba(255, 255, 255, 0.637);\n    cursor: pointer;\n}\n\n.editBtn:active,.deleteBtn:active {\n    background-color: rgb(255, 249, 249);\n}\n\n.checkCon {\n    position: absolute;\n    margin-right: 15px;\n}\n\n.checkCon:hover * {\n    background-color: #e9e9e9;\n}\n\ninput[type=checkbox] {\n    position: absolute;\n    opacity: 1;\n    height: 50;\n    width: 50;\n}\n\n.checkmark {\n    position: absolute;\n    top: 0;\n    left: 0;\n    height: 20px;\n    width: 20px;\n    background-color: #ffffff;\n    border: 1px solid var(--btn-border-color);\n    border-radius: 5px;\n}\n\n.checkCon input:checked ~ .checkmark {\n    background-color: var(--btn-bg-color);\n}\n\n.checkmark:after {\n  content: \"\";\n  position: absolute;\n  display: none;\n}\n\n.checkCon input:checked ~ .checkmark:after {\n    display: block;\n}\n\n.checkCon .checkmark:after {\n  left: 6px;\n  top: 3px;\n  width: 5px;\n  height: 10px;\n  border: solid white;\n  border-width: 0 3px 3px 0;\n  -webkit-transform: rotate(45deg);\n  -ms-transform: rotate(45deg);\n  transform: rotate(45deg);\n}\n\n.complete {\n    opacity: 0.6 !important;\n}\n\n\nfooter {\n    font-size: min(1.2rem,30px);\n    line-height: 1.5rem;\n    background-color: rgb(187, 98, 113);\n    color: white;\n    font-weight: bold;\n    text-align: center;\n    padding: 15px;\n}\n\n\n\n/* New todo modal */\n::backdrop {\n    background: rgb(206, 200, 200);\n    opacity: 0.50;\n}\n\ndialog {\n    padding: 0px;\n    border: 3px solid grey;\n    border-radius: 15px;\n    font-size: 1.5rem;\n}\n\ndialog > div {\n    padding: 24px;\n}\n\ndialog input {\n    margin-top: 10px;\n    border: 0px;\n    border-bottom: 2px solid lightgrey;\n    padding: 10px 20px 10px;\n    font-size: 1.5rem;\n}\n\nlabel[for=\"desc\"], label[for=\"editDesc\"] {\n    display: block;\n}\n\ntextarea {\n    display: block;\n    font-family: \"Roboto\",\"Arial\";\n    font-size: 1.2rem;\n    resize: none;\n}\n\ninput:hover {\n    background-color: rgb(235, 235, 235);\n}\n\ndialog input:focus {\n    outline: none;\n    border: 0px;\n    border-bottom: 2px solid lightgrey;\n}\n\n#priorityCon {\n    display: flex;\n    gap: 15px;\n    align-items: center;\n}\n\n#priorityCon > button {\n    font-size: 1.2rem;\n    border-radius: 15px;\n    padding: 3px 5px 3px;\n}\n\n#priorityCon > button.selected {\n    /* background-color: rgb(223, 223, 223); */\n    border: 5px solid var(--btn-border-color);\n    position: relative;\n    top: -2px;\n    left: -2px;\n    box-shadow: 5px 5px 13px 3px rgba(0,0,0,0.34);\n}\n\ndialog button:active {\n    position: relative;\n    top: 1px;\n    left: 1px;\n}\n\n#trivialBtn {\n    background-color: rgb(199, 245, 199);\n    border: 5px solid rgb(40, 126, 40);\n}\n\n#trivialBtn:hover {\n    background-color: rgb(168, 214, 168);\n}\n\n\n\n#standardBtn {\n    background-color: rgb(245, 237, 199);\n    border: 5px solid rgb(126, 113, 40);\n}\n\n#standardBtn:hover {\n    background-color: rgb(214, 206, 168);\n}\n\n\n\n#imptBtn {\n    background-color: rgb(245, 199, 199);\n    border: 5px solid rgb(126, 40, 40);\n}\n\n#imptBtn:hover {\n    background-color: rgb(214, 171, 168);\n}\n\n\n\n.buttongrp {\n    display: flex;\n    justify-content: right;\n}\n\nbutton.submit,button.edit {\n    padding: 7px;\n    font-size: 1.2rem;\n    background-color: var(--btn-bg-color);\n    font-weight: bold;\n    color: white;\n    border-radius: 10px;\n    border: 3px solid  var(--btn-border-color);\n}\n\nbutton.submit:hover,button.edit:hover {\n    background-color: var(--btn-hover-color);\n}\n\n#Pcontainer {\n    display: flex;\n    gap: 20px;\n    align-items: center;\n}\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\nselect, ::picker(select) {\n    appearance: base-select;\n}\n\nselect {\n    border: 2px solid #ddd;\n    border-radius: 15px;\n    font-size: 1.2rem;\n    padding: 5px;\n    width: 7rem;\n}\n\nselect:hover {\n    background: rgb(216, 186, 186);\n\n}\n\nselect::picker-icon {\n    rotate: 180deg;\n}\n\n::picker(select) {\n  border: none;\n}\n\noption {\n    display: flex;\n    justify-content: flex-start;\n    gap: 20px;\n    padding: 5px 3px 5px;\n    border: 2px solid #ddd;\n    background: #eee;\n    border-radius: 10px;\n}\n\noption:hover {\n    background: rgb(216, 186, 186);\n}\n\noption:focus {\n    background: rgb(216, 186, 186);\n}\n\nselectedcontent .icon {\n  display: none;\n}"],"sourceRoot":""}]);
